'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { useAuth } from '@/lib/auth-context'
import { apiFetch } from '@/lib/api'
import { useToast } from '@/hooks/use-toast'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Switch } from '@/components/ui/switch'
import { Label } from '@/components/ui/label'
import { Slider } from '@/components/ui/slider'
import { Input } from '@/components/ui/input'
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
} from '@/components/ui/dialog'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { ArrowLeft, Bell, MapPin, Mail, Shield, Moon, Sun, Laptop } from 'lucide-react'
import { useTheme } from 'next-themes'

interface UserSettings {
  notifySightings: boolean
  notifyNearby: boolean
  notifyMessages: boolean
  shareLocation: boolean
  autoLocation: boolean
  emailUpdates: boolean
  emailTips: boolean
  alertRadius: number
}

const defaultSettings: UserSettings = {
  notifySightings: true,
  notifyNearby: true,
  notifyMessages: true,
  shareLocation: true,
  autoLocation: true,
  emailUpdates: false,
  emailTips: false,
  alertRadius: 10,
}

export default function SettingsPage() {
  const { user, isLoading } = useAuth()
  const router = useRouter()
  const { toast } = useToast()
  const { theme, setTheme } = useTheme()
  const [settings, setSettings] = useState<UserSettings>(defaultSettings)
  const [loadingSettings, setLoadingSettings] = useState(true)
  
  // Password Change State
  const [changePasswordOpen, setChangePasswordOpen] = useState(false)
  const [currentPassword, setCurrentPassword] = useState('')
  const [newPassword, setNewPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [isChangingPassword, setIsChangingPassword] = useState(false)

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login')
    }
  }, [user, isLoading, router])

  useEffect(() => {
    if (user) {
      apiFetch(`/api/usuarios/${user.id}`)
        .then((userData: any) => {
          if (userData.configuracoes) {
             setSettings({ ...defaultSettings, ...userData.configuracoes })
          }
        })
        .catch(err => console.error(err))
        .finally(() => setLoadingSettings(false))
    }
  }, [user])

  const updateSetting = async (key: keyof UserSettings, value: any) => {
    const newSettings = { ...settings, [key]: value }
    setSettings(newSettings)

    try {
      await apiFetch(`/api/usuarios/${user!.id}/config`, {
        method: 'PATCH',
        body: JSON.stringify({ configuracoes: newSettings })
      })
    } catch (error) {
      console.error('Failed to update settings', error)
      setSettings(settings)
      toast({
        title: "Erro",
        description: "Não foi possível salvar a configuração.",
        variant: "destructive"
      })
    }
  }

  const handleChangePassword = async (e: React.FormEvent) => {
    e.preventDefault()
    if (newPassword !== confirmPassword) {
      toast({
        title: "Erro",
        description: "A nova senha e a confirmação não coincidem.",
        variant: "destructive"
      })
      return
    }

    setIsChangingPassword(true)
    try {
      await apiFetch(`/api/usuarios/${user!.id}/password`, {
        method: 'PATCH',
        body: JSON.stringify({ senhaAtual: currentPassword, novaSenha: newPassword })
      })
      
      toast({
        title: "Sucesso",
        description: "Sua senha foi alterada com sucesso.",
      })
      setChangePasswordOpen(false)
      setCurrentPassword('')
      setNewPassword('')
      setConfirmPassword('')
    } catch (error: any) {
      console.error('Failed to change password', error)
      toast({
        title: "Erro",
        description: error.message || "Não foi possível alterar a senha. Verifique sua senha atual.",
        variant: "destructive"
      })
    } finally {
      setIsChangingPassword(false)
    }
  }

  if (isLoading || loadingSettings) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4" />
          <p className="text-muted-foreground">Carregando...</p>
        </div>
      </div>
    )
  }

  if (!user) {
    return null
  }

  return (
    <div className="min-h-screen bg-linear-to-br from-teal-50 to-orange-50 dark:from-slate-950 dark:to-slate-900">

      <main className="container mx-auto px-4 py-6 space-y-4 max-w-2xl">
        <div className="mb-3 flex items-center gap-3">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => router.push('/profile')}
            className="p-2"
          >
            <ArrowLeft className="w-5 h-5" />
          </Button>
          <h1 className="text-xl font-bold">Configurações</h1>
        </div>

        {/* Appearance */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Sun className="w-5 h-5 rotate-0 scale-100 transition-all dark:-rotate-90 dark:scale-0" />
              <Moon className="absolute w-5 h-5 rotate-90 scale-0 transition-all dark:rotate-0 dark:scale-100" />
              <span className="ml-0 dark:ml-7 transition-all">Aparência</span>
            </CardTitle>
            <CardDescription>Personalize a aparência do aplicativo</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label>Tema</Label>
                <p className="text-sm text-muted-foreground">Selecione o tema de sua preferência</p>
              </div>
              <Select value={theme} onValueChange={setTheme}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="Selecione o tema" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="light">
                    <div className="flex items-center gap-2">
                      <Sun className="w-4 h-4" /> Claro
                    </div>
                  </SelectItem>
                  <SelectItem value="dark">
                    <div className="flex items-center gap-2">
                      <Moon className="w-4 h-4" /> Escuro
                    </div>
                  </SelectItem>
                  <SelectItem value="system">
                    <div className="flex items-center gap-2">
                      <Laptop className="w-4 h-4" /> Sistema
                    </div>
                  </SelectItem>
                </SelectContent>
              </Select>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Bell className="w-5 h-5" />
              Notificações
            </CardTitle>
            <CardDescription>Gerencie suas preferências de notificação</CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label htmlFor="notify-sightings">Avistamentos</Label>
                <p className="text-sm text-muted-foreground">Receber notificações de avistamentos dos seus pets</p>
              </div>
              <Switch 
                id="notify-sightings" 
                checked={settings.notifySightings}
                onCheckedChange={(checked) => updateSetting('notifySightings', checked)}
              />
            </div>
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label htmlFor="notify-nearby">Pets próximos</Label>
                <p className="text-sm text-muted-foreground">Alertas de pets perdidos/encontrados perto de você</p>
              </div>
              <Switch 
                id="notify-nearby" 
                checked={settings.notifyNearby}
                onCheckedChange={(checked) => updateSetting('notifyNearby', checked)}
              />
            </div>
            
            {settings.notifyNearby && (
              <div className="space-y-3 pt-2 border-t">
                <div className="flex justify-between">
                  <Label>Raio de Alerta</Label>
                  <span className="text-sm text-muted-foreground">{settings.alertRadius} km</span>
                </div>
                <Slider
                  defaultValue={[settings.alertRadius]}
                  max={100}
                  min={1}
                  step={1}
                  onValueChange={(vals) => updateSetting('alertRadius', vals[0])}
                />
                <p className="text-xs text-muted-foreground">
                  Você receberá alertas de pets dentro deste raio de distância.
                </p>
              </div>
            )}

            <div className="flex items-center justify-between pt-2 border-t">
              <div className="space-y-0.5">
                <Label htmlFor="notify-messages">Mensagens</Label>
                <p className="text-sm text-muted-foreground">Notificações de mensagens e contatos</p>
              </div>
              <Switch 
                id="notify-messages" 
                checked={settings.notifyMessages}
                onCheckedChange={(checked) => updateSetting('notifyMessages', checked)}
              />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Shield className="w-5 h-5" />
              Segurança
            </CardTitle>
            <CardDescription>Gerencie a segurança da sua conta</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label>Senha</Label>
                <p className="text-sm text-muted-foreground">Altere sua senha periodicamente para manter sua conta segura</p>
              </div>
              <Dialog open={changePasswordOpen} onOpenChange={setChangePasswordOpen}>
                <DialogTrigger asChild>
                  <Button variant="outline" size="sm">Alterar Senha</Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Alterar Senha</DialogTitle>
                    <DialogDescription>
                      Digite sua senha atual e a nova senha desejada.
                    </DialogDescription>
                  </DialogHeader>
                  <form onSubmit={handleChangePassword} className="space-y-4 py-4">
                    <div className="space-y-2">
                      <Label htmlFor="current-password">Senha Atual</Label>
                      <Input
                        id="current-password"
                        type="password"
                        value={currentPassword}
                        onChange={(e) => setCurrentPassword(e.target.value)}
                        required
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="new-password">Nova Senha</Label>
                      <Input
                        id="new-password"
                        type="password"
                        value={newPassword}
                        onChange={(e) => setNewPassword(e.target.value)}
                        required
                        minLength={8}
                      />
                      <p className="text-xs text-muted-foreground">Mínimo de 8 caracteres</p>
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="confirm-password">Confirmar Nova Senha</Label>
                      <Input
                        id="confirm-password"
                        type="password"
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        required
                      />
                    </div>
                    <DialogFooter>
                      <Button type="button" variant="outline" onClick={() => setChangePasswordOpen(false)}>
                        Cancelar
                      </Button>
                      <Button type="submit" disabled={isChangingPassword}>
                        {isChangingPassword ? 'Alterando...' : 'Salvar Nova Senha'}
                      </Button>
                    </DialogFooter>
                  </form>
                </DialogContent>
              </Dialog>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <MapPin className="w-5 h-5" />
              Localização
            </CardTitle>
            <CardDescription>Configurações de localização e privacidade</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label htmlFor="share-location">Compartilhar localização</Label>
                <p className="text-sm text-muted-foreground">Permitir que outros vejam sua região aproximada</p>
              </div>
              <Switch 
                id="share-location" 
                checked={settings.shareLocation}
                onCheckedChange={(checked) => updateSetting('shareLocation', checked)}
              />
            </div>
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label htmlFor="auto-location">Detecção automática</Label>
                <p className="text-sm text-muted-foreground">Usar sua localização para sugestões personalizadas</p>
              </div>
              <Switch 
                id="auto-location" 
                checked={settings.autoLocation}
                onCheckedChange={(checked) => updateSetting('autoLocation', checked)}
              />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Mail className="w-5 h-5" />
              E-mail
            </CardTitle>
            <CardDescription>Preferências de comunicação por e-mail</CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label htmlFor="email-updates">Atualizações</Label>
                <p className="text-sm text-muted-foreground">Receber resumos semanais por e-mail</p>
              </div>
              <Switch 
                id="email-updates" 
                checked={settings.emailUpdates}
                onCheckedChange={(checked) => updateSetting('emailUpdates', checked)}
              />
            </div>
            <div className="flex items-center justify-between">
              <div className="space-y-0.5">
                <Label htmlFor="email-tips">Dicas e novidades</Label>
                <p className="text-sm text-muted-foreground">Receber dicas sobre como encontrar pets</p>
              </div>
              <Switch 
                id="email-tips" 
                checked={settings.emailTips}
                onCheckedChange={(checked) => updateSetting('emailTips', checked)}
              />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Privacidade e Dados</CardTitle>
            <CardDescription>Gerencie seus dados e privacidade</CardDescription>
          </CardHeader>
          <CardContent className="space-y-2">
            <Button variant="outline" className="w-full justify-start">
              Baixar meus dados
            </Button>
            <Button variant="outline" className="w-full justify-start text-red-600 hover:text-red-700">
              Excluir conta
            </Button>
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
